import { Location } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const locationServices = {
  getLocationList: async (queryParams) => {
    try {
      const payload = {
        ...Location.allLocation,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
