const Banner = {
  allProduct: {
    url: "/product",
    method: "GET",
  },
  addProduct: {
    url: "/product",
    method: "POST",
  },
  updateProduct: (id) => ({
    url: `/product/${id}`,
    method: "PUT",
  }),
  deleteProduct: (id) => ({
    url: `/product/${id}`,
    method: "DELETE",
  }),
};
export default Banner;
